/*CUSTOM NORMALIZER SCRIPT
* Forked from Bootstrap Reboot v4.1.3 (https://getbootstrap.com/)
* Licensed under MIT (https://github.com/twbs/bootstrap/blob/master/LICENSE)
* Forked from Normalize.css, licensed MIT (https://github.com/necolas/normalize.css/blob/master/LICENSE.md)*/
:root {
    --success: #28a745;
    --info: #17a2b8;
    --warning: #ffc107;
    --danger: #dc3545;
    --font-family-sans-serif: "segoe UI", "Helvetica Neue", sans-serif, -apple-system, BlinkMacSystemFont, Roboto, Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

/*Structural*/
*, *:before, *:after {box-sizing: border-box;
margin: 0px;
box-align: start;}

html {font-family: "segoe UI", "Helvetica Neue", sans-serif, -apple-system, BlinkMacSystemFont, Roboto, Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
line-height: 1.15;
-webkit-text-size-adjust: 100%;
-ms-text-size-adjust: 100%;
-ms-overflow-style: scrollbar;
-webkit-tap-highlight-color: rgba(0, 0, 0, 0);}

@-ms-viewport {width: device-width;}

body {font-size: 16px; font-size: 1rem;
font-weight: 400;
line-height: 1.5;
text-align: left;
background-color: #fff;}

article, aside, details, div, figcaption, figure, footer, header, hgroup, legend, main, nav, p, section, summary {display: block;}

[tabindex="-1"]:focus {outline: 0 !important;}

img {border-style: none;
vertical-align: middle;}

figure {margin: 0 0 1em; margin: 0 0 1rem;}

svg {overflow: hidden;
vertical-align: middle;}

table {border-collapse: collapse;}

caption {padding-top: .75em; padding-top: .75rem;
padding-bottom: .75em; padding-bottom: .75rem;
color: var(--lightgray);
text-align: center;
caption-side: top;}

th {text-align: inherit;}

ol, ul, dl {margin: 0;
margin-bottom: 1em; margin-bottom: 1rem;}

ol ol, ul ul, ol ul, ul ol {margin-bottom: 0;}

li {margin: 0;}

dt {font-weight: 700;}

dd {margin-bottom: .5em; margin-bottom: .5rem;
margin-left: 0;}

/*Semantics*/
h1 {font-size: 2em; font-size: 2rem;
margin: .67em 0; margin: .67rem 0;}

hr {box-sizing: content-box;
height: 0;
overflow: visible;}

h1, h2, h3, h4, h5, h6 {margin-top: 0.5em; margin-top: 0.5rem;
margin-bottom: 0.5em; margin-bottom: 0.5rem;}

p {margin-top: 0;
margin-bottom: 1em; margin-bottom: 1rem;}

pre { margin-top: 0;
margin-bottom: 1em; margin-bottom: 1rem;
overflow: auto;
-ms-overflow-style: scrollbar;}

abbr[title], abbr[data-original-title] {text-decoration: underline;
-webkit-text-decoration: underline dotted;
text-decoration: underline dotted;
cursor: help;
border-bottom: none;}

b, strong {font-weight: bolder;}

pre, code, kbd, samp {font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
font-size: 1em; font-size: 1rem; }

small {font-size: 80%; font-size: .8rem;}

sub, sup { font-size: 75%; font-size: .75rem;
line-height: 0;
position: relative;
vertical-align: baseline;}

sub {bottom: -0.25em; bottom: -0.25rem;}

sup {top: -0.5em; top: -0.5rem;}

blockquote {margin: 0 0 1em; margin: 0 0 1rem;}

dfn {font-style: italic;}

address {margin-bottom: 1em; margin-bottom: 1rem;
font-style: normal;
line-height: inherit;}

/*Input*/
button, input, optgroup, select, textarea {display: block;
font-family: inherit;
font-size: 100%;
line-height: 1.15;
margin: 0;}

button, input {overflow: visible;}

button, select {text-transform: none;}

button {border-radius: 0; border-radius: inherit;}

button:focus {outline: 1px dotted;
outline: 5px auto -webkit-focus-ring-color;}

button, [type="button"], [type="reset"], [type="submit"] {-webkit-appearance: button;}

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {border-style: none;
padding: 0;}

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {outline: 1px dotted ButtonText;}

input[type="date"], input[type="time"], input[type="datetime-local"], input[type="month"] {-webkit-appearance: listbox;}

textarea {overflow: auto;
resize: vertical;}

fieldset {min-width: 0; padding: 0; margin: 0; border: 0;}

label {display: inline-block;
margin-bottom: 5px;}

legend {box-sizing: border-box;
width: 100%;
max-width: 100%;
padding: 0;
margin-bottom: 10px;
font-size: 2.5em; font-size: 2.5rem;
line-height: inherit;
color: inherit;
white-space: normal;}

[type="checkbox"], [type="radio"] {box-sizing: border-box; padding: 0;}

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {height: auto;}

[type="search"] {-webkit-appearance: textfield;
outline-offset: -2px;}

[type="search"]::-webkit-search-decoration {-webkit-appearance: none;}

[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration {-webkit-appearance: none;}

::-webkit-file-upload-button {-webkit-appearance: button;
font: inherit;}

/*Links*/
a {text-decoration: none;
background-color: transparent;
-webkit-text-decoration-skip: objects;}

a:hover {text-decoration: none;}

a:not([href]):not([tabindex]) {color: inherit;
text-decoration: none;}

a:not([href]):not([tabindex]):hover, a:not([href]):not([tabindex]):focus {color: inherit;
text-decoration: none;}

a:not([href]):not([tabindex]):focus {outline: 0;}

/*Others*/
summary {display: list-item;}

template {display: none;}

[hidden] {display: none;}

output {display: inline-block;}

progress {vertical-align: baseline;}
