/* use material design component styles */
/* @use "@material/button/mdc-button" */

.pointer1, .pointer2, .pointer3 {animation: swipe 4s;
  animation-iteration-count: infinite;
}

.pointer2 {animation-delay: 0.07s}

.pointer3 {animation-delay: 0.14s}

@keyframes swipe {
  0% {opacity: 0;
    }

  10% {
      opacity: 1;
      transform: scale(1.2)
    }

  20% {
      opacity: 1;
      transform: scale(1)
    }
  95% {opacity: 1}
  100% {opacity: 0}
}

@keyframes move-aim {
  0% {
    right: 0%;
    top: 0vh
  }

  10% {
    right: -2%;
    top: -5vh
  }

  20% {
    right: 5%;
    top: 1vh
  }

  30% {
    right: -5%;
    top: -1vh
  }

  40% {
    right: 0%;
    top: -4vh
  }

  70% {
    right: 0%;
    top: 0.5vh
  }

  100%{
    right: 0%;
    top: 0vh
  }
}


.App {
  text-align: center;
}

.App * {overflow: hidden !important;}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
