:root {
    --white: #e4e9f4;
    --lightblue: #82add6;
    --blue: #06397d;
    --yellow: #facb66;
    --red: #911f1e;
}

.white {background: var(--white) !important}
.white--text {color: var(--white) !important}

.lightblue {background: var(--lightblue) !important}
.lightblue--text {color: var(--lightblue) !important}


.blue {background: var(--blue) !important}
.blue--text {color: var(--blue) !important}


.yellow {background: var(--yellow) !important}
.yellow--text {color: var(--yellow) !important}


.red {background: var(--red) !important}
.red--text {color: var(--red) !important}


.transparent {background: transparent !important}
